@import "../variables";
.header {
  width: 100%;
  position: fixed;
  z-index: 100;
  background-color: #ffffff;
  a {
    color: $primary-text-color; }
  &__menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px 20px; }
  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
      font-size: 24px;
      fill: $basic-text-color; } }

  &__logo {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: $large-font-size; }

  &__slogan {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    line-height: 16px;
    font-weight: 300;
    color: $basic-text-color; } }
