@import "../variables";

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    margin-top: 20px;
    min-width: 320px;
    border-radius: 15px;
    box-shadow: 0 0px 8px rgba(0, 0, 0, 0.2);
    fieldset {
        margin: 10px; }
    label {
        margin-left: 10px; }
    &__legend {
        margin-bottom: 15px;
        text-align: center;
        font-size: $large-font-size;
        font-weight: normal; }
    &__input {
        margin: 10px 0;
        padding: 0 15px;
        width: 100%;
        height: 50px;
        font-size: $medium-font-size;
        border-radius: 4px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
        background-color: $basic-background-color; }
    &__textarea {
        width: 100%;
        margin: 10px 0;
        padding: 15px;
        text-align: left;
        font-weight: 400;
        border-radius: 4px;
        border: none;
        resize: none;
        font-size: $medium-font-size;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
        background-color: $basic-background-color; }
    &__submit {
        margin: 10px 0;
        padding: 0 15px;
        width: 100%;
        height: 50px;
        font-size: $medium-font-size;
        border-radius: 4px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
        background-color: $basic-background-color; } }
