@import "variables";
@include font('Stratos-Regular', 'stratos-regular');
@include font('Stratos-Medium', 'stratos-medium');
@include font('Stratos-Bold', 'stratos-bold');

@mixin fontSR {
  font-family: 'Stratos-Regular', sans-serif;
  font-style: normal;
  font-weight: 400; }

@mixin fontSM {
  font-family: 'Stratos-Medium', sans-serif;
  font-style: normal;
  font-weight: 600; }
@mixin fontSB {
  font-family: 'Stratos-Bold', sans-serif;
  font-style: normal;
  font-weight: 700; }
::placeholder {
    @include fontSR; }
body {
  @include fontSR;
  width: 100vw;
  min-width: 320px; }
.out {
  width: 100%; }

.wrapper {
  width: 100%;
  min-width: 320px;
  padding: $basic-padding; }

h1 {
  @include fontSB; }

button {
  background-color: transparent; }

a {
  text-decoration: none; }

.padding {
  height: 75px;
  margin-top: -20px;
  background-color: $basic-background-color; }

